$mainColor: #ff0000;

@mixin styleFun() {
  background-color: #eeefff;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

.red {
  color: $mainColor;
}

.my-alert {
  @include styleFun();

  p {
    margin-bottom: 0;
    color: blue;
  }
}

.my-alert-blue {
  @extend .my-alert;
  background-color: #aaddff;
}

.wow-enter {
  opacity: 0;
}
.wow-enter-active {
  opacity: 1;
  transition: all 1500ms;
}


